.main-nav {
  display: flex;
  justify-content: center;
  width: 100%; }
  @media (max-width: 1023px) {
    .main-nav {
      display: block; } }

.main-nav__list {
  width: 100%;
  max-width: 960px;
  min-width: 880px;
  margin: 0 19.5%;
  display: flex;
  justify-content: space-between;
  padding: 21px 21px 9px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }
  @media (max-width: 1023px) {
    .main-nav__list {
      min-width: 100%;
      flex-wrap: wrap; } }

.main-nav__item a {
  display: block;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: 0.3px;
  font-weight: 300;
  color: #ffffff;
  opacity: 0.7; }
  .main-nav__item a:hover {
    opacity: 1; }
  @media (max-width: 1023px) {
    .main-nav__item a {
      width: 100%; } }

@media (max-width: 1023px) {
  .nojs .main-nav__list {
    display: block;
    margin: 0;
    padding: 20px 0; } }

@media (max-width: 1023px) {
  .nojs .main-nav__item a {
    display: block;
    position: relative;
    margin-top: 30px;
    color: #48494d;
    font-size: 20px;
    line-height: 60px;
    font-weight: 400;
    background-color: #f6f6f6;
    padding: 0 16px;
    opacity: 1; }
    .nojs .main-nav__item a::after {
      content: "";
      position: absolute;
      right: 23px;
      top: 50%;
      height: 7px;
      width: 7px;
      border-bottom: 2px solid rgba(44, 46, 63, 0.5);
      border-right: 2px solid rgba(44, 46, 63, 0.5);
      transform: rotate(-45deg) translateY(-50%); } }

.mobile-nav {
  display: none; }
  @media (max-width: 1023px) {
    .mobile-nav {
      display: flex;
      justify-content: space-between;
      width: 100%; } }

.mobile-nav__toggler {
  display: flex;
  justify-content: center;
  width: 50px;
  height: 50px;
  background-color: #0ad9c6;
  border: none;
  cursor: pointer; }

.mobile-nav__toggler > span {
  position: relative; }

.mobile-nav__toggler > span::before,
.mobile-nav__toggler > span::after,
.mobile-nav__toggler > span {
  width: 22px;
  height: 1px;
  background-color: #000000; }

.mobile-nav__toggler > span::before,
.mobile-nav__toggler > span::after {
  content: "";
  position: absolute;
  right: 0; }

.mobile-nav__toggler > span::after {
  top: 7px; }

.mobile-nav__toggler > span::before {
  bottom: 7px; }

@media (max-width: 1023px) {
  .nojs .mobile-nav {
    justify-content: flex-end; } }

.nojs .mobile-nav__toggler {
  background-color: #ffffff;
  transition: background-color 0s 0.3s; }

.nojs .mobile-nav__toggler > span::before,
.nojs .mobile-nav__toggler > span::after {
  width: 30px;
  background-color: rgba(44, 46, 63, 0.5);
  top: 0; }

.nojs .mobile-nav__toggler > span {
  background-color: transparent; }

.nojs .mobile-nav__toggler > span::after {
  transform: rotate(45deg); }

.nojs .mobile-nav__toggler > span::before {
  transform: rotate(-45deg); }

